<template>
  <VModal class="justify-content-end" position="right" :show="show">
    <form @submit.prevent="onSubmit">
      <ModalContainer
        v-if="show"
        :title="title"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <AuthorsAutocomplete
            :label="localize.tabAuthorsModalAuthorAddFio.text"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            :placeholder="localize.commonFormPlaceholderRus.text"
            popover-position="middle-right"
            v-model="author.russianName"
            name="author.russianName"
            root-class="mb-2"
            @select="onSelect"
          />

          <div class="d-flex flex-fill flex-column justify-content-center">
            <VTypography
              class="text-center text-white author-modal__link"
              v-html="infoText"
            />
          </div>
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import { mapGetters } from "vuex";

import VModal from "@/components/modals/Modal";
import VTypography from "@/components/ui/Typography";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import AuthorsAutocomplete from "@/components/authors/AuthorsAutocomplete";
import { formatString } from "@/utils/FormatUtils";

export default {
  name: "AuthorModal",
  components: {
    VModal,
    VTypography,
    ActionButton,
    ModalContainer,
    AuthorsAutocomplete,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      errors: {},
      authorFull: undefined,
      author: {},
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    title() {
      return Object.keys(this.initialValues).length > 0
        ? this.localize.commonFormAuthorChange.text
        : this.localize.commonFormAuthorAdd.text;
    },
    infoText() {
      return formatString(
        this.localize.tabAuthorsModalAuthorAddFioWarning.text,
        this.localize.tabAuthorsModalAuthorAddFioWarning.params
      );
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue && !prevValue) {
        this.author = { ...this.initialValues };
      }
    },
  },
  methods: {
    onSelect(author) {
      this.author = author;
      this.authorFull = author;
    },
    onSubmit(event) {
      this.errors = {};

      const hasRussianName = !!this.author.russianName;

      if (!hasRussianName) {
        this.errors = {
          russianName: this.localize.commonFormRusTextFillingError.title,
        };
      }

      if (hasRussianName) {
        this.$emit("submit", {
          author: this.author,
          authorFull: this.authorFull,
        });
        this.author = {};

        event.target.reset();
      }
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/styles/variables.scss";

.author-modal__popover {
  background-color: $gray3;
}

.author-modal__link {
  a {
    color: $white;
    text-decoration: none;
  }
}
</style>
