<template>
  <div class="container">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ localize.tabDisclosureBlkDateIntendedPublication.text }}
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleasePlaned"
      :disabled="!editable"
      class="mb-5"
      :items="[
        { key: true, title: localize.commonFormYes.text },
        { key: false, title: localize.commonFormNo.text },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleasePlaned"
      class="mb-1"
    >
      {{ localize.tabDisclosureBlkDateIntendedPublicDate.text }}
    </TypographyWithInfo>

    <DatePicker
      v-if="hasReleasePlaned"
      v-model="releasePlanedDate"
      :disabled="!editable"
      class="mb-5"
    />

    <div class="row" v-if="hasReleasePlaned">
      <div class="col">
        <TextInput
          v-model="releasePlanedRussianInformation"
          :label="localize.tabDisclosureBlkDateIntendedPublicOtherInfo.text"
          :placeholder="localize.commonFormPlaceholderRus.text"
          :disabled="!editable"
        />
      </div>
      <div class="col">
        <TextInput
          v-model="releasePlanedEnglishInformation"
          :placeholder="localize.commonFormPlaceholderEng.text"
          :disabled="!editable"
          root-class="mt-4"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import DatePicker from "@/components/date-picker/DatePicker";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "DisclosurePlanedRelease",
  components: {
    TextInput,
    DatePicker,
    RadioGroup,
    TypographyWithInfo,
  },
  props: {
    editable: Boolean,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("disclosure", [
      "getHasReleasePlaned",
      "getReleasePlanedDate",
      "getReleasePlanedRussianInformation",
      "getReleasePlanedEnglishInformation",
    ]),
    hasReleasePlaned: {
      get() {
        return this.getHasReleasePlaned;
      },
      set(value) {
        this.changeHasReleasePlaned(value);
      },
    },
    releasePlanedDate: {
      get() {
        return this.getReleasePlanedDate;
      },
      set(value) {
        this.changeReleasePlanedDate(value);
      },
    },
    releasePlanedRussianInformation: {
      get() {
        return this.getReleasePlanedRussianInformation;
      },
      set(value) {
        this.changeReleasePlanedRussianInformation(value);
      },
    },
    releasePlanedEnglishInformation: {
      get() {
        return this.getReleasePlanedEnglishInformation;
      },
      set(value) {
        this.changeReleasePlanedEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleasePlaned",
      "changeReleasePlanedDate",
      "changeReleasePlanedRussianInformation",
      "changeReleasePlanedEnglishInformation",
    ]),
  },
};
</script>
