<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import InnInput from "@/components/ui/InnInput";
import PhoneInput from "@/components/ui/PhoneInput";
import DivisionCodeInput from "@/components/ui/DivisionCodeInput";
import { mapGetters } from "vuex";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { formatOtherAuthorErrors } from "@/helpers/FormsHelpers";
import SnilsInput from "@/components/ui/SnilsInput";

export default {
  name: "OtherAuthorModal",
  components: {
    SnilsInput,
    TypographyWithInfo,
    DivisionCodeInput,
    PhoneInput,
    InnInput,
    DatePicker,
    RadioGroup,
    TextInput,
    VTypography,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      errors: {},
      author: { isCitizen: true },
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    title() {
      return Object.keys(this.initialValues).length > 1
        ? this.localize.commonFormAuthorChange.text
        : this.localize.commonFormAuthorAdd.text;
    },
    buttonTitle() {
      return Object.keys(this.initialValues).length > 1
        ? this.localize.commonFormAuthorChange.text
        : this.localize.commonFormAnotherAuthorAdd.text;
    },
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      this.errors = formatOtherAuthorErrors(this.author, this.localize);

      if (Object.keys(this.errors).length === 0) {
        this.$emit("submit", this.author);
        this.author = {};
      }
    },
  },
};
</script>

