<template>
  <div class="d-flex flex-column mb-7">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ titleText }}

      <template v-slot:popover>
        <PopoverContent :width="529">
          <VTypography font-weight="medium">{{ titlePrompt }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      class="mb-5"
      :disabled="!editable"
      v-model="withOtherAuthors"
      :items="[
        { key: true, title: localize.commonFormInvolved.text },
        { key: false, title: localize.commonFormNotInvolved.text },
      ]"
    />

    <AuthorsTable
      @edit="onEdit"
      :list="authors"
      @delete="onDelete"
      :editable="!!editable"
      v-if="authors.length > 0"
      class="other-authors__table"
    />

    <AddButton
      class="mt-5"
      v-if="withOtherAuthors && editable"
      @click="showModal = true"
    >
      {{ localize.commonFormAuthorAdd.text }}
    </AddButton>

    <OtherAuthorModal
      :show="showModal"
      @submit="onSubmit"
      @close="showModal = false"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import AddButton from "@/components/buttons/AddButton";
import AuthorsTable from "@/components/authors/AuthorsTable";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import OtherAuthorModal from "@/components/authors/OtherAuthorModal";
import { FormTypes } from "@/constants/FormConstants";

export default {
  name: "OtherAuthors",
  components: {
    AuthorsTable,
    PopoverContent,
    AddButton,
    RadioGroup,
    VTypography,
    OtherAuthorModal,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
      editedAuthorIndex: undefined,
    };
  },
  props: {
    editable: Boolean,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["getType"]),
    ...mapGetters("authors", {
      authors: "getOtherAuthors",
    }),
    titleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabAuthorsBlkOtherAuthorsTech.text;
      }

      return this.localize.tabAuthorsBlkOtherAuthorsSoft.text;
    },
    titlePrompt() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabAuthorsBlkOtherAuthorsTech.prompt;
      }

      return this.localize.tabAuthorsBlkOtherAuthorsSoft.prompt;
    },
    withOtherAuthors: {
      get() {
        return this.$store.state.authors.withOtherAuthors;
      },
      set(value) {
        this.changeWithOtherAuthors(value);
      },
    },
  },
  methods: {
    ...mapMutations("authors", [
      "addOtherAuthor",
      "updateOtherAuthor",
      "removeOtherAuthor",
      "changeWithOtherAuthors",
    ]),
    onEdit(index) {
      this.editedAuthorIndex = index;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeOtherAuthor(index);
    },
    onSubmit(author) {
      if (this.editedAuthorIndex >= 0) {
        this.updateOtherAuthor({ index: this.editedAuthorIndex, author });
        this.editedAuthorIndex = undefined;
      } else {
        this.addOtherAuthor(author);
      }

      this.$emit("success");

      this.showModal = false;
    },
  },
};
</script>
