<template>
  <FormsContainer class="authors-form">
    <VTypography font-weight="heavy" font-style="header-1" class="mb-5">{{
      title
    }}</VTypography>
    <InstituteAuthors
      class="mb-7"
      :editable="editable"
      @success="$emit('success')"
    />

    <OtherAuthors
      class="mb-7"
      :editable="editable"
      @success="$emit('success')"
    />

    <AuthorsCreativeTable :editable="editable" />
  </FormsContainer>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import OtherAuthors from "@/components/authors/OtherAuthors";
import FormsContainer from "@/components/forms/FormsContainer";
import InstituteAuthors from "@/components/authors/InstituteAuthors";
import AuthorsCreativeTable from "@/components/authors/AuthorsCreativeTable";
import { mapGetters } from "vuex";
import { FormsTabKeys, FormTypes } from "@/constants/FormConstants";

export default {
  name: "AuthorsForm",
  components: {
    VTypography,
    OtherAuthors,
    FormsContainer,
    InstituteAuthors,
    AuthorsCreativeTable,
  },
  emits: {
    success: Function,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm", "getType"]),
    editable() {
      return this.canEditForm(FormsTabKeys.Authors);
    },
    title() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabAuthorsTitleTech.text;
      }

      return this.localize.tabAuthorsTitleSoft.text;
    },
  },
};
</script>
