<template>
  <FormsContainer class="container">
    <VTypography class="mb-5" font-weight="heavy" font-style="header-1">
      {{ localize.tabCommon.text }}
    </VTypography>

    <div class="row mb-2">
      <div class="col">
        <TextInput
          :label="localize.tabCommonBlkSent.text"
          :disabled="!editable"
          :placeholder="localize.commonFormPlaceholderRus.text"
          v-model="russianInformation"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium">{{
                localize.tabCommonBlkSent.prompt
              }}</VTypography>
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          v-model="englishInformation"
          root-class="mt-4"
          :disabled="!editable"
          :placeholder="localize.commonFormPlaceholderEng.text"
        />
      </div>
    </div>

    <div class="row mb-2">
      <div class="col">
        <TextInput
          type="email"
          v-model="email"
          :multiline="false"
          placeholder="Email"
          :disabled="!editable"
        />
      </div>
    </div>

    <div class="row">
      <div class="col">
        <TextInput
          :label="
            isTechForm
              ? localize.tabCommonBlkTechnology.text
              : localize.tabCommonBlkComputerProgram.text
          "
          :disabled="!editable"
          v-model="russianTechnology"
          :placeholder="localize.commonFormPlaceholderRus.text"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium">{{
                isTechForm
                  ? localize.tabCommonBlkTechnology.prompt
                  : localize.tabCommonBlkComputerProgram.prompt
              }}</VTypography>
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          root-class="mt-4"
          :disabled="!editable"
          v-model="englishTechnology"
          :placeholder="localize.commonFormPlaceholderEng.text"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import { FormsTabKeys, FormTypes } from "@/constants/FormConstants";

export default {
  name: "GeneralForm",
  components: {
    TextInput,
    VTypography,
    PopoverContent,
    FormsContainer,
  },
  methods: {
    ...mapMutations("general", [
      "changeEmail",
      "changeRussianTechnology",
      "changeEnglishTechnology",
      "changeRussianInformation",
      "changeEnglishInformation",
    ]),
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm", "getFormType"]),
    ...mapGetters("general", [
      "getEmail",
      "getRussianTechnology",
      "getEnglishTechnology",
      "getEnglishInformation",
      "getRussianInformation",
    ]),
    isTechForm() {
      return this.getFormType === FormTypes.Tech;
    },
    editable() {
      return this.canEditForm(FormsTabKeys.General);
    },
    russianInformation: {
      get() {
        return this.getRussianInformation;
      },
      set(value) {
        this.changeRussianInformation(value);
      },
    },
    englishInformation: {
      get() {
        return this.getEnglishInformation;
      },
      set(value) {
        this.changeEnglishInformation(value);
      },
    },
    email: {
      get() {
        return this.getEmail;
      },
      set(value) {
        this.changeEmail(value);
      },
    },
    russianTechnology: {
      get() {
        return this.getRussianTechnology;
      },
      set(value) {
        this.changeRussianTechnology(value);
      },
    },
    englishTechnology: {
      get() {
        return this.getEnglishTechnology;
      },
      set(value) {
        this.changeEnglishTechnology(value);
      },
    },
  },
};
</script>
