<template>
  <div
    @click="onHideCalendar"
    class="d-flex flex-column position-relative"
    v-click-outside="onHideCalendar"
  >
    <div
      class="d-flex align-items-center justify-content-between position-relative px-2 date-picker__input-container"
      :class="containerClasses"
      @mouseenter="hover = true"
      @mouseleave="hover = false"
    >
      <div class="d-flex align-items-center">
        <input
          :value="day"
          maxlength="2"
          ref="dayInput"
          @focus="onFocus"
          :multiple="false"
          :disabled="$attrs.disabled"
          @blur="onBlur($event, 'day')"
          @keyup="onKeyUp($event, 'day')"
          @input="day = $event.target.value"
          @keydown="onKeyDown($event, 'day')"
          class="p-0 date-picker__input"
          :class="{
            'date-picker__input--placeholder': Number(day) === 0,
            'text-white': Number(day) > 0 && inputType === 'contrast',
          }"
        />
        <VTypography class="date-picker__divider">.</VTypography>
        <input
          :value="month"
          maxlength="2"
          ref="monthInput"
          @focus="onFocus"
          :multiple="false"
          :disabled="$attrs.disabled"
          @blur="onBlur($event, 'month')"
          class="p-0 date-picker__input"
          @keyup="onKeyUp($event, 'month')"
          @input="month = $event.target.value"
          @keydown="onKeyDown($event, 'month')"
          :class="{
            'date-picker__input--placeholder': Number(month) === 0,
            'text-white': Number(month) > 0 && inputType === 'contrast',
          }"
        />
        <VTypography class="date-picker__divider" :class="inputClass"
          >.</VTypography
        >
        <input
          :value="year"
          maxlength="4"
          ref="yearInput"
          @focus="onFocus"
          :multiple="false"
          :disabled="$attrs.disabled"
          @blur="onBlur($event, 'year')"
          @keyup="onKeyUp($event, 'year')"
          @input="year = $event.target.value"
          @keydown="onKeyDown($event, 'year')"
          class="p-0 date-picker__input date-picker__input-year"
          :class="{
            'date-picker__input--placeholder': Number(year) === 0,
            'text-white': Number(year) > 0 && inputType === 'contrast',
          }"
        />
      </div>

      <VButton :disabled="$attrs.disabled" @click="onShowCalendar">
        <CalendarIcon />
      </VButton>
    </div>

    <VTypography
      v-if="!!error"
      font-style="text-2"
      class="date-picker__error"
      >{{ error }}</VTypography
    >

    <DatePickerCalendar
      :date="dateTime"
      v-if="showCalendar"
      @select="onSelectDate"
      @close="onHideCalendar"
    />
  </div>
</template>

<script>
import { DateTime } from "luxon";
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";
import CalendarIcon from "@/components/icons/CalendarIcon";
import DatePickerCalendar from "@/components/date-picker/DatePickerCalendar";

export default {
  name: "DatePicker",
  components: {
    VButton,
    VTypography,
    CalendarIcon,
    DatePickerCalendar,
  },
  props: {
    error: String,
    modelValue: String,
    inputClass: String,
    inputType: {
      type: String,
      validator(value) {
        return ["default", "contrast"].includes(value);
      },
    },
  },
  data() {
    const baseData = {
      focus: false,
      hover: false,
      showCalendar: false,
    };

    if (this.modelValue) {
      const dateTime = DateTime.fromISO(this.modelValue);

      return {
        day: dateTime.day < 10 ? `0${dateTime.day}` : `${dateTime.day}`,
        year: `${dateTime.year}`,
        month: dateTime.month < 10 ? `0${dateTime.month}` : `${dateTime.month}`,
        ...baseData,
      };
    }

    return {
      day: "00",
      month: "00",
      year: "0000",
      ...baseData,
    };
  },
  computed: {
    dateTime() {
      const date = DateTime.now();

      if (
        Number(this.day) > 0 &&
        Number(this.month) > 0 &&
        Number(this.year) > 0
      ) {
        return date.set({
          day: this.day,
          year: this.year,
          month: this.month,
        });
      }

      return date;
    },
    containerClasses() {
      return {
        "date-picker__input-container--focus": this.focus,
        "date-picker__input-container--error": !!this.error,
        "border-white": !this.error && this.inputType === "contrast",
        "date-picker__input-container--disabled": !!this.$attrs.disabled,
        "date-picker__input-container--hover":
          ((!this.focus && this.hover) || (!this.focus && this.modelValue)) &&
          !this.$attrs.disabled,
      };
    },
  },
  methods: {
    onShowCalendar(event) {
      event.stopPropagation();

      this.showCalendar = true;
    },
    onHideCalendar() {
      this.showCalendar = false;
    },
    onSelectDate(date) {
      this.day = date.day < 10 ? `0${date.day}` : `${date.day}`;
      this.year = `${date.year}`;
      this.month = date.month < 10 ? `0${date.month}` : `${date.month}`;

      this.onValidateDate();
    },
    onBlur(event, type) {
      this.focus = false;

      const { value } = event.target;

      const valueNumber = Number(value);

      const isLessTen = valueNumber < 10;
      const isZero = valueNumber === 0;

      switch (type) {
        case "day": {
          const isMore = valueNumber > 31;

          if (isMore) {
            this.day = "31";
          }

          if (isLessTen) {
            this.day = `0${valueNumber}`;
          }

          if (isZero) {
            this.day = "01";
          }

          break;
        }

        case "month": {
          const isMore = valueNumber > 12;

          if (isMore) {
            this.month = "12";
          }

          if (isLessTen) {
            this.month = `0${valueNumber}`;
          }

          if (isZero) {
            this.month = "01";
          }

          break;
        }
      }
    },
    onFocus(event) {
      this.focus = true;
      event.target.setSelectionRange(0, event.target.value.length);
    },
    onValidateDate() {
      const dateTime = DateTime.fromObject({
        day: this.day,
        month: this.month,
        year: this.year,
      });

      if (
        this.day !== "00" &&
        this.month !== "00" &&
        this.year !== "0000" &&
        dateTime.invalid == null
      ) {
        this.$emit("update:modelValue", dateTime.toISO());
      }
    },
    onKeyUp(event, type) {
      const value = event.target.value;
      const valueLength = value.length;
      const isDigit = event.code.indexOf("Digit") === 0;

      if (isDigit) {
        if (type === "day") {
          if (valueLength === 2 && value !== "00") {
            this.$refs.monthInput.focus();
          }
        } else if (type === "month") {
          if (valueLength === 2 && value !== "00") {
            this.$refs.yearInput.focus();
          }
        }
      }

      this.onValidateDate();
    },
    onKeyDown(event) {
      const isTab = event.code === "Tab";
      const isBackspace = event.code === "Backspace";
      const isDigit = event.code.indexOf("Digit") === 0;

      if (!isBackspace && !isDigit && !isTab) {
        event.preventDefault();
      }
    },
  },
};
</script>

