  border-color: $gray1;
}

.date-picker__input-container--focus {
  border-color: $gray1;
}

.date-picker__input--placeholder {
  color: $gray4;
}

.date-picker__input-container--disabled {
  border-color: $gray5;
  background-color: $gray5;
}

.date-picker__input-container--error {
  border-color: $red;
  margin-bottom: 5px;
}

.date-picker__input {
  width: 20px;
  margin-left: 1px;
  border: none;
  outline: none;
  background-color: transparent;
  font-weight: $font-weight-medium;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    margin: 0;
