    -moz-appearance: textfield;
  }
}

.date-picker__input--placeholder {
  color: $gray4;
}

.date-picker__input-year {
  width: 40px;
}

.date-picker__divider {
  color: $gray4;
}

.date-picker__error {
  color: $red;
}
</style>
