<template>
  <div class="d-flex flex-column">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ titleText }}

      <template v-slot:popover>
        <PopoverContent :width="529">
          <VTypography font-weight="medium">{{ titlePrompt }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <AuthorsTable
      @edit="onEdit"
      :list="authors"
      @delete="onDelete"
      v-if="authors.length > 0"
      :editable="!!editable"
    />

    <AddButton class="mt-5" v-if="editable" @click="showModal = true">
      {{ localize.commonFormAuthorAdd.text }}
    </AddButton>

    <AuthorModal
      :show="showModal"
      @submit="onSubmit"
      @close="onCloseModal"
      :initial-values="editedAuthor"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import VTypography from "@/components/ui/Typography";
import AddButton from "@/components/buttons/AddButton";
import AuthorModal from "@/components/authors/AuthorModal";
import AuthorsTable from "@/components/authors/AuthorsTable";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { FormTypes } from "@/constants/FormConstants";

export default {
  name: "InstituteAuthors",
  components: {
    AuthorsTable,
    VTypography,
    PopoverContent,
    AddButton,
    AuthorModal,
    TypographyWithInfo,
  },
  emits: {
    success: Function,
  },
  props: {
    editable: Boolean,
  },
  data() {
    return {
      showModal: false,
      editedAuthor: undefined,
      editedAuthorIndex: undefined,
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["getType"]),
    ...mapGetters("authors", {
      authors: "getInstituteAuthors",
    }),
    titleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabAuthorsBlkAuthorAddTech.text;
      }

      return this.localize.tabAuthorsBlkAuthorAddSoft.text;
    },
    titlePrompt() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabAuthorsBlkAuthorAddTech.prompt;
      }

      return this.localize.tabAuthorsBlkAuthorAddSoft.prompt;
    },
  },
  methods: {
    ...mapMutations("authors", [
      "addInstituteAuthor",
      "removeInstituteAuthor",
      "updateInstituteAuthor",
    ]),
    onSubmit({ author, authorFull }) {
      if (this.editedAuthorIndex >= 0) {
        this.updateInstituteAuthor({
          index: this.editedAuthorIndex,
          author: {
            ...this.editedAuthor,
            ...authorFull,
            name: author,
          },
        });
      } else {
        this.addInstituteAuthor({
          ...this.editedAuthor,
          ...authorFull,
          name: author,
        });
      }

      this.showModal = false;
      this.editedAuthor = undefined;
      this.editedAuthorIndex = undefined;

      this.$emit("success");
    },
    onEdit(index) {
      this.editedAuthorIndex = index;
      this.editedAuthor = this.authors[index].name;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeInstituteAuthor(index);
    },
    onCloseModal() {
      this.showModal = false;
      this.editedAuthor = undefined;
      this.editedAuthorIndex = undefined;
    },
  },
};
</script>
