<template>
  <div class="container">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ localize.tabDisclosureBlkOrallyWritingThirdParties.text }}
    </TypographyWithInfo>

    <TypographyWithInfo font-weight="medium" class="mb-1">
      {{ titleText }}

      <template v-slot:popover>
        <PopoverContent :width="489">
          <VTypography font-weight="medium">{{ titlePrompt }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleaseThirdParties"
      :disabled="!editable"
      class="mb-5"
      :items="[
        { key: true, title: localize.commonFormYes.text },
        { key: false, title: localize.commonFormNo.text },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleaseThirdParties"
      class="mb-1"
    >
      {{ localize.tabDisclosureBlkOrallyWritingThirdPartiesDateEarliest.text }}
    </TypographyWithInfo>

    <DatePicker
      :disabled="!editable"
      v-if="hasReleaseThirdParties"
      v-model="releaseThirdPartiesDate"
      class="mb-5"
    >
    </DatePicker>

    <div class="row" v-if="hasReleaseThirdParties">
      <TypographyWithInfo class="mb-1" font-style="text-1" font-weight="medium">
        {{
          localize.tabDisclosureBlkOrallyWritingThirdPartiesInfoAboutEarliest
            .text
        }}

        <template v-slot:popover>
          <PopoverContent :width="440">
            <VTypography font-weight="medium">{{
              localize
                .tabDisclosureBlkOrallyWritingThirdPartiesInfoAboutEarliest
                .prompt
            }}</VTypography>
          </PopoverContent>
        </template>
      </TypographyWithInfo>
    </div>

    <div class="row" v-if="hasReleaseThirdParties">
      <div class="col">
        <TextInput
          :disabled="!editable"
          v-model="releaseThirdPartiesRussianInformation"
          :placeholder="localize.commonFormPlaceholderRus.text"
        />
      </div>
      <div class="col">
        <TextInput
          :disabled="!editable"
          v-model="releaseThirdPartiesEnglishInformation"
          :placeholder="localize.commonFormPlaceholderEng.text"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { FormTypes } from "@/constants/FormConstants";

export default {
  name: "DisclosureThirdParties",
  components: {
    TextInput,
    DatePicker,
    RadioGroup,
    VTypography,
    PopoverContent,
    TypographyWithInfo,
  },
  props: {
    editable: Boolean,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["getType"]),
    ...mapGetters("disclosure", [
      "getHasReleaseThirdParties",
      "getReleaseThirdPartiesDate",
      "getReleaseThirdPartiesRussianInformation",
      "getReleaseThirdPartiesEnglishInformation",
    ]),
    titleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabDisclosureBlkOrallyWritingThirdPartiesQstTech
          .text;
      }

      return this.localize.tabDisclosureBlkOrallyWritingThirdPartiesQstSoft
        .text;
    },
    titlePrompt() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabDisclosureBlkOrallyWritingThirdPartiesQstTech
          .text;
      }

      return this.localize.tabDisclosureBlkOrallyWritingThirdPartiesQstSoft
        .text;
    },
    hasReleaseThirdParties: {
      get() {
        return this.getHasReleaseThirdParties;
      },
      set(value) {
        this.changeHasReleaseThirdParties(value);
      },
    },
    releaseThirdPartiesDate: {
      get() {
        return this.getReleaseThirdPartiesDate;
      },
      set(value) {
        this.changeReleaseThirdPartiesDate(value);
      },
    },
    releaseThirdPartiesRussianInformation: {
      get() {
        return this.getReleaseThirdPartiesRussianInformation;
      },
      set(value) {
        this.changeReleaseThirdPartiesRussianInformation(value);
      },
    },
    releaseThirdPartiesEnglishInformation: {
      get() {
        return this.getReleaseThirdPartiesEnglishInformation;
      },
      set(value) {
        this.changeReleaseThirdPartiesEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleaseThirdParties",
      "changeReleaseThirdPartiesDate",
      "changeReleaseThirdPartiesRussianInformation",
      "changeReleaseThirdPartiesEnglishInformation",
    ]),
  },
};
</script>
