<template>
  <div class="container">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ titleText }}
    </TypographyWithInfo>

    <TypographyWithInfo font-weight="medium" class="mb-1">
      {{ localize.tabDisclosureBlkInfoPriorSubmissionDateQst.text }}

      <template v-slot:popover>
        <PopoverContent :width="489">
          <VTypography font-weight="medium">{{
            localize.tabDisclosureBlkInfoPriorSubmissionDateQst.prompt
          }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleaseBefore"
      :disabled="!editable"
      class="mb-5"
      :items="[
        { key: true, title: localize.commonFormYes.text },
        { key: false, title: localize.commonFormNo.text },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleaseBefore"
      class="mb-1"
    >
      {{ localize.tabDisclosureBlkInfoPriorSubmissionDateDateEarliest.text }}
    </TypographyWithInfo>

    <DatePicker
      v-if="hasReleaseBefore"
      v-model="releaseBeforeDate"
      class="mb-5"
      :disabled="!editable"
    />

    <div class="row" v-if="hasReleaseBefore">
      <div class="col">
        <TextInput
          v-model="releaseBeforeRussianInformation"
          :label="
            localize.tabDisclosureBlkInfoPriorSubmissionDateInfoAboutEarliest
              .text
          "
          :placeholder="localize.commonFormPlaceholderRus.text"
          :disabled="!editable"
        >
          <template v-slot:popover>
            <PopoverContent :width="440">
              <VTypography font-weight="medium">{{
                localize
                  .tabDisclosureBlkInfoPriorSubmissionDateInfoAboutEarliest
                  .prompt
              }}</VTypography>
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          :disabled="!editable"
          v-model="releaseBeforeEnglishInformation"
          :placeholder="localize.commonFormPlaceholderEng.text"
          root-class="mt-4"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { FormTypes } from "@/constants/FormConstants";

export default {
  name: "DisclosureBeforeSubmission",
  components: {
    TextInput,
    DatePicker,
    RadioGroup,
    VTypography,
    PopoverContent,
    TypographyWithInfo,
  },
  props: {
    editable: Boolean,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["getType"]),
    ...mapGetters("disclosure", [
      "getHasReleaseBefore",
      "getReleaseBeforeDate",
      "getReleaseBeforeRussianInformation",
      "getReleaseBeforeEnglishInformation",
    ]),
    titleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabDisclosureBlkInfoPriorSubmissionDateTech.text;
      }

      return this.localize.tabDisclosureBlkInfoPriorSubmissionDateSoft.text;
    },
    hasReleaseBefore: {
      get() {
        return this.getHasReleaseBefore;
      },
      set(value) {
        this.changeHasReleaseBefore(value);
      },
    },
    releaseBeforeDate: {
      get() {
        return this.getReleaseBeforeDate;
      },
      set(value) {
        this.changeReleaseBeforeDate(value);
      },
    },
    releaseBeforeRussianInformation: {
      get() {
        return this.getReleaseBeforeRussianInformation;
      },
      set(value) {
        this.changeReleaseBeforeRussianInformation(value);
      },
    },
    releaseBeforeEnglishInformation: {
      get() {
        return this.getReleaseBeforeEnglishInformation;
      },
      set(value) {
        this.changeReleaseBeforeEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleaseBefore",
      "changeReleaseBeforeDate",
      "changeReleaseBeforeRussianInformation",
      "changeReleaseBeforeEnglishInformation",
    ]),
  },
};
</script>
