<template>
  <FormsContainer>
    <VTypography font-weight="heavy" font-style="header-1" class="mb-5">{{
      localize.tabKeyDates.text
    }}</VTypography>

    <TypographyWithInfo font-weight="medium" class="mb-1">
      {{ releaseTitleText }}

      <template v-slot:popover>
        <PopoverContent :width="363">
          <VTypography font-weight="medium">{{
            releaseTitlePrompt
          }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker class="mb-5" v-model="releaseDate" :disabled="!editable" />

    <TypographyWithInfo font-weight="medium" class="mb-1">
      {{ developmentTitleText }}

      <template v-slot:popover>
        <PopoverContent :width="440">
          <VTypography font-weight="medium">{{
            developmentTitlePrompt
          }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker v-model="developmentDate" :disabled="!editable" />
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { FormsTabKeys, FormTypes } from "@/constants/FormConstants";

export default {
  name: "DatesForm",
  components: {
    DatePicker,
    TypographyWithInfo,
    PopoverContent,
    VTypography,
    FormsContainer,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm", "getType"]),
    ...mapGetters("dates", ["getStartReleaseDate", "getStartDevelopmentDate"]),
    editable() {
      return this.canEditForm(FormsTabKeys.KeyDates);
    },
    releaseTitleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabKeyDatesBlkProjectStartDateTech.text;
      }

      return this.localize.tabKeyDatesBlkProjectStartDateSoft.text;
    },
    releaseTitlePrompt() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabKeyDatesBlkProjectStartDateTech.prompt;
      }

      return this.localize.tabKeyDatesBlkProjectStartDateSoft.prompt;
    },
    developmentTitleText() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabKeyDatesBlkDevelopStartDateTech.text;
      }

      return this.localize.tabKeyDatesBlkDevelopStartDateSoft.text;
    },
    developmentTitlePrompt() {
      if (this.getType === FormTypes.Tech) {
        return this.localize.tabKeyDatesBlkDevelopStartDateTech.prompt;
      }

      return this.localize.tabKeyDatesBlkDevelopStartDateSoft.prompt;
    },
    releaseDate: {
      get() {
        return this.getStartReleaseDate;
      },
      set(date) {
        this.changeReleaseDate(date);
      },
    },
    developmentDate: {
      get() {
        return this.getStartDevelopmentDate;
      },
      set(date) {
        this.changeDevelopmentDate(date);
      },
    },
  },
  methods: {
    ...mapMutations("dates", ["changeReleaseDate", "changeDevelopmentDate"]),
  },
};
</script>
